import React, { useState } from 'react'
import { Box, Button, Drawer, Typography, Card, CardMedia, TextField } from '@mui/material'
import { Speaker1, Speaker2, Speaker3, Speaker4, FeaturedImageDark as FeaturedImage } from '../../assets'
import { useCreateEventApi } from '../../hooks/setLocalStorage';


export const Moderators = ({ moderators }) => {

    const {
        mod, setMod,
        createModerators, 
        mode,
        setState, state
    } = useCreateEventApi();

    const onTextChange = (key) => (event) => {
        setMod(prev => {
            return {
                ...prev,
                [key]: event.target.value
            }
        });
    };

    const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        setState({ ...state, [anchor]: open });
    };

    const moderator_shell = mode?.length ? mode : moderators;

    return (
        <Box sx={{ paddingBlockStart: "1rem" }}>
            <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                <Box sx={{ display: "flex", gap: "1rem" }}>
                    {
                       moderator_shell.map(moderator => (
                            <Box key={moderator} sx={{ width: "15%", height: "200px" }}>
                                <Card>
                                    <Box sx={{ borderBlockRadius: "10px", height: "200px" }}>
                                        <CardMedia
                                            component="img"
                                            sx={{ height: "100%" }}
                                            image={Speaker4}
                                            alt="Gen. Press Release"
                                        />
                                    </Box>
                                </Card>
                            </Box>)
                        )
                    }

                    <Box
                        onClick={toggleDrawer("right", true)}
                        sx={{
                            width: "20%",
                            display: "flex",
                            backgroundColor: "#fff",
                            border: "2px dashed #939393",
                            padding: "16px",
                            justifyContent: "center",
                            alignItems: "center",
                            borderRadius: "8px",
                            cursor: 'pointer'
                        }}>
                        {/* <Box sx={{
                            display: "flex",
                            alignItems: "center",
                            // backgroundColor: "#EFEFEF"
                        }}> */}
                        <Typography fontSize={10} color={'#4D4D4D'} noWrap>Add Moderators</Typography>
                        {/* </Box> */}
                    </Box>
                </Box>
            </Box>

            <Drawer
                anchor={"right"}
                open={state["right"]}
                onClose={toggleDrawer("right", false)}
                variant='temporary'
            >
                <Box
                    role="presentation"
                    sx={{
                        backgroundColor: "white",
                        height: "100%",
                        display: "flex",
                        // gap: "2rem",
                        flexDirection: "column",
                        width: 400
                    }}>
                    <Box sx={{
                        display: "flex", flexDirection: "column", justifyContent: "center",
                        alignItems: "center", backgroundColor: "#fff",
                        border: "2px dashed #000", padding: "4rem"
                    }}>
                        {FeaturedImage}
                        <Typography>Featured image</Typography>
                    </Box>

                    <Box
                        component="form"
                        sx={{
                            display: "flex",
                            flexDirection: "column",
                            paddingInline: "2rem",
                            paddingBlock: "1rem",
                            justifyContent: 'space-between',
                            height: "100%"
                        }}
                    >
                        <Box>
                            <Box
                                sx={{
                                    '& > :not(style)': { m: 1, width: '25ch' },
                                }}
                                noValidate
                                autoComplete="off"
                            >
                                <Typography>Moderator's Name</Typography>
                                <TextField
                                    id="outlined-select-document"
                                    // label="Enter title here"
                                    variant="standard"
                                    onChange={onTextChange("name")} value={mod.name} />
                            </Box>
                            <Box
                                sx={{
                                    '& > :not(style)': { m: 1, width: '25ch' },
                                }}
                                noValidate
                                autoComplete="off"
                            >
                                <Typography>Rank</Typography>
                                <TextField
                                    id="outlined-select-document"
                                    // label="rank here"
                                    variant="standard" onChange={onTextChange("rank")} value={mod.rank} />
                            </Box>
                            <Box
                                sx={{
                                    '& > :not(style)': { m: 1, width: '25ch' },
                                }}
                                noValidate
                                autoComplete="off"
                            >
                                <Typography>Description</Typography>
                                <TextField
                                    id="outlined-select-document"
                                    // label="Enter description here"
                                    variant="standard" onChange={onTextChange("description")} value={mod.description} />
                            </Box>
                        </Box>

                        <Box sx={{ display: "flex", alignSelf: "flex-end", flexDirection: "column" }}>
                            <Button
                                variant="contained"
                                size='large'
                                onClick={createModerators}
                                sx={{
                                    color: "#fff",
                                    fontSize: "12px",
                                    textTransform: "none",
                                    backgroundColor: "#000",
                                    paddingBlock: 0,
                                    cursor: "pointer",
                                    "&:hover": {
                                        // backgroundColor: "#000"
                                    }
                                }}
                            >Create</Button>
                        </Box>
                    </Box>
                </Box>
            </Drawer>
        </Box >
    )
}
