import { useState } from "react"
import { Box, Typography, Button, Stack, Card, CardMedia, IconButton, TextField } from "@mui/material";
import { ArrowBackIos } from '@mui/icons-material';
import {
    participantLogo,
    Delete, Edit,
    EventsModule, FeaturedImage, FeaturedImageDark
} from '../../assets';
import { Instructions } from '../Instructions';
import { Programme } from '../Programme';
import { Keynote } from '../KeyNote';
import { ParticipantList } from '../ParticipantList';
import { useCreateEventApi } from "../../hooks/setLocalStorage";


const streamObjects = [{
    svg: EventsModule.EventsChat,
    svg_active: EventsModule.EventsChatActive,
    title: "Event Chat",
    id: "1",
    alias: "chat"
}, {
    svg: EventsModule.EventParticipants,
    svg_active: EventsModule.EventParticipantsActive,
    title: "Participants",
    id: "2",
    alias: "default"
}, {
    svg: EventsModule.EventKeyNoteSpeaker,
    svg_active: EventsModule.EventKeyNoteSpeakerActive,
    title: "Keynote Speakers",
    id: "3",
    alias: "keynote"
}, {
    svg: EventsModule.EventProgram,
    svg_active: EventsModule.EventProgramActive,
    title: "Programme",
    id: "4",
    alias: "program"
}, {
    svg: EventsModule.EventAdminInstructions,
    svg_active: EventsModule.EventAdminInstructionsActive,
    title: "Admin Instructions",
    id: "5",
    alias: "instructions"
}];

export const ParticipantView = ({ closeModal, data, setData, deleteEvent, setTrigger }) => {
    const [active, setActive] = useState("default");
    const [edit, setEdit] = useState(false);

    const { updateEvent } = useCreateEventApi();

    const toggleCardReader = key => (event) => {
        if (event.target.value === "title" || "description" || "venue") {
            setData(prev => ({ ...prev, [key]: event.target.value }))
        }
    }

    const start_date = new Date(data.start_date).toDateString();
    const end_date = new Date(data.end_date).toDateString();

    const VIEW = {
        chat: <ParticipantList data={data} />,
        default: <ParticipantList data={data} />,
        keynote: <Keynote data={data} />,
        program: <Programme data={data} />,
        instructions: <Instructions data={data} />,
        demo: <Typography fontSize={16} fontWeight={400}>
            <em>Demo</em>
        </Typography>
    };

    const displacer = () => streamObjects.map((stream) => {
        return (
            <Box
                sx={{
                    height: "100%",
                    padding: "1.1rem",
                    display: "flex",
                    gap: "0.3rem",
                    backgroundColor: active !== stream.alias ? "#fff" : "#1C274C",
                    color: active !== stream.alias ? "#1C274C" : "#fff",
                    "&:hover": {
                        backgroundColor: active !== stream.alias ? "#EFEFEF" : "#1C274C",
                        color: active !== stream.alias ? "#1C274C" : "#fff",
                    },
                    flexGrow: 1,
                    alignItems: "center",
                    justifyContent: "center",
                    cursor: "pointer"
                }}
                key={stream.id}
                onClick={() => setActive(stream.alias)}
            >
                {active !== stream.alias ? stream.svg : stream.svg_active}
                <Box>
                    <Typography fontSize={12}>
                        {stream.title}
                    </Typography>
                </Box >
            </Box>
        );
    })

    return (
        <Box
            role="presentation"
            sx={{
                padding: "2.5rem",
                display: "flex",
                height: "100vh",
                // alignSelf: "flex-end",
                width: 1000,
            }}
        >
            <Box
                sx={{
                    display: 'flex',
                    gap: "1rem",
                    flexDirection: "column",
                    width: "100%"
                }}
            >
                <Box sx={{ display: "flex", justifyContent: "space-between" }}>
                    <Box sx={{ display: "flex", cursor: "pointer" }} onClick={closeModal} >
                        <ArrowBackIos />
                        <Typography>Back</Typography>
                    </Box>
                    <Box sx={{ display: "flex", gap: ".5rem" }}>
                        <Box sx={{
                            // width: "10%",
                        }}>
                            <Stack>
                                <Button
                                    variant="contained"
                                    size="small"
                                    onClick={(event) => deleteEvent(event, { id: data.id, closeModal })}
                                    startIcon={
                                        <Box>
                                            {Delete}
                                        </Box>
                                    }
                                    sx={{
                                        border: "1px solid #BC1300",
                                        color: "#BC1300",
                                        fontSize: "8px",
                                        textTransform: "none",
                                        backgroundColor: "#fff",
                                        paddingBlock: 0,
                                        "&:hover": {
                                            backgroundColor: "#fff"
                                        },

                                    }}
                                >Delete</Button>
                            </Stack>
                        </Box>
                        <Box sx={{
                            // width: "10%",
                        }}>
                            <Stack>
                                <Button
                                    variant="contained"
                                    size="small"
                                    startIcon={
                                        <Box>
                                            {Edit}
                                        </Box>
                                    }
                                    onClick={() => setEdit(prev => !prev)}
                                    sx={{
                                        color: "#fff",
                                        fontSize: "8px",
                                        textTransform: "none",
                                        backgroundColor: "#1C274C",
                                        paddingBlock: 0,
                                        "&:hover": {
                                            backgroundColor: "#000"
                                        }
                                    }}
                                >Edit</Button>
                            </Stack>
                        </Box>
                    </Box>
                </Box>

                {!edit ?
                    (<Box sx={{ display: "flex", gap: "3rem" }}>
                        <Box sx={{ height: "100%", backgroundColor: "#f8f8f8" }}>
                            <Card>
                                <Box sx={{ height: 300, width: 300 }}>
                                    <CardMedia
                                        component="img"
                                        sx={{ height: "100%" }}
                                        image={data.image}
                                        alt="event_logo"
                                    />
                                </Box>
                            </Card>
                        </Box>
                        <Box sx={{ display: "flex", flexDirection: "column", gap: "1rem", justifyContent: "center" }}>
                            <Box>
                                <Typography variant={"subtitle2"} color={"#919191"} fontSize={12}>
                                    Title
                                </Typography>
                                <Typography>
                                    {data.title}
                                </Typography>
                            </Box>
                            <Box>
                                <Typography variant={"subtitle2"} color={"#919191"} fontSize={12}>
                                    About the Event
                                </Typography>
                                <Typography>
                                    {data.description}
                                </Typography>
                            </Box>
                            <Box sx={{ display: "flex", gap: "3rem" }}>
                                <Box>
                                    <Typography variant='subtitle1' color={"#919191"} fontSize={12}>
                                        Start Date/End date
                                    </Typography>
                                    <Typography color={'#1A1A1A'} fontSize={16}>
                                        {start_date} - {end_date}
                                    </Typography>

                                </Box>
                                <Box>
                                    <Typography variant='subtitle1' color={"#919191"} fontSize={12}>
                                        Venue
                                    </Typography>
                                    <Typography color={'#1A1A1A'} fontSize={16}>
                                        {data.venue}
                                    </Typography>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                    ) :
                    (<Box sx={{ display: "flex", gap: "3rem" }}>
                        <Box sx={{ height: "100%", backgroundColor: "#f8f8f8" }}>
                            <Card sx={{
                                height: 300,
                                width: 300,
                            }}>
                                <Box sx={{
                                    display: "flex",
                                    flexDirection: "column",
                                    justifyContent: "center",
                                    // backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(${data.image})`,
                                    // backgroundRepeat: 'no-repeat',
                                    alignItems: "center",
                                    height: "100%",
                                    objectFit: "auto"
                                }}>
                                    <IconButton>
                                        {FeaturedImageDark}
                                    </IconButton>
                                    <Typography
                                        color={"#7C7C7C"}
                                        fontSize={14}
                                        fontWeight={600}
                                    >edit image</Typography>
                                </Box>

                            </Card>
                        </Box>
                        <Box sx={{ display: "flex", flexDirection: "column", gap: "1rem", justifyContent: "center" }}>
                            <Box
                                sx={{
                                    '& > :not(style)': { m: 1 },
                                }}
                                noValidate
                                autoComplete="off"
                            >
                                <Typography>Title</Typography>
                                <TextField
                                    id="outlined-select-document"
                                    placeholder="Sun, July 15 2023"
                                    variant="standard"
                                    value={data.title}
                                    onChange={toggleCardReader("title")}
                                    fullWidth
                                />
                            </Box>

                            <Box
                                sx={{
                                    '& > :not(style)': { m: 1 },
                                }}
                                noValidate
                                autoComplete="off"
                            >
                                <Typography>About the Event</Typography>
                                <TextField
                                    id="outlined-select-document"
                                    placeholder="Sun, July 15 2023"
                                    variant="standard"
                                    value={data.description}
                                    onChange={toggleCardReader("description")}
                                    fullWidth
                                />
                            </Box>
                            <Box sx={{ display: "flex", gap: "3rem" }}>
                                <Box>
                                    <Typography variant='subtitle1' color={"#919191"} fontSize={12}>
                                        Start Date/End date
                                    </Typography>
                                    <Typography color={'#1A1A1A'} fontSize={16}>
                                        {start_date} - {end_date}
                                    </Typography>
                                </Box>

                                <Box
                                    sx={{
                                        '& > :not(style)': { m: 1 },
                                    }}
                                    noValidate
                                    autoComplete="off"
                                >
                                    <Typography>Venue</Typography>
                                    <TextField
                                        id="outlined-select-document"
                                        placeholder="Sun, July 15 2023"
                                        variant="standard"
                                        value={data.venue}
                                        onChange={toggleCardReader("venue")}
                                        fullWidth
                                    />
                                </Box>
                            </Box>
                        </Box>
                    </Box>)
                }

                <Box sx={{ backgroundColor: "#fff", display: "flex", justifyContent: "space-between" }}>
                    {displacer}
                </Box>

                <Box sx={{
                    display: "flex",
                    flexDirection: "column",
                    gap: "1rem",
                    overflow: "scroll",

                }}>
                    {VIEW[0]}
                </Box>

                <Box sx={{
                    width: "10%",
                }}>
                    {edit && <Stack>
                        <Button
                            onClick={(event) => updateEvent(event, { payload: data, closeModal, setTrigger })}
                            variant="contained"
                            size="small"
                            startIcon={
                                <Box>
                                    {Edit}
                                </Box>
                            }
                            sx={{
                                color: "#fff",
                                fontSize: "8px",
                                textTransform: "none",
                                backgroundColor: "#1C274C",
                                paddingBlock: 0,
                                height: 25,
                                "&:hover": {
                                    backgroundColor: "#7C7C7C"
                                }
                            }}
                        >Update</Button>
                    </Stack>}
                </Box>
            </Box>
        </Box >
    )
};
