import React, { useState } from 'react'
import { Box, Button, Typography, Card, CardMedia, Drawer, TextField } from '@mui/material'
import { Speaker1, Speaker2, Speaker3, Speaker4, AddParticipant, FeaturedImageDark as FeaturedImage } from '../assets'


export const Keynote = ({ data }) => {
    const [state, setState] = useState("");

    const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        setState({ ...state, [anchor]: open });
    };

    return (
        <>
            <Box sx={{ display: "flex", flexDirection: "column", gap: "3rem" }}>
                <Box sx={{ display: "flex", flexDirection: "column", gap: "1rem" }}>
                    <Box sx={{ display: "flex", flexDirection: "column", width: "25%", alignSelf: "flex-end" }}>
                        <Box sx={{ justifyContent: "flex-end", display: "flex", gap: "1rem", width: "100%" }}>

                            <Box
                                onClick={toggleDrawer("right", true)}
                                sx={{ display: "flex", gap: ".2rem", alignItems: "center", backgroundColor: "#EFEFEF", borderRadius: "8px", padding: "8px" }}
                            >
                                {AddParticipant}
                                <Typography fontSize={10} color={'#4D4D4D'}>Add keynote speaker</Typography>
                            </Box>
                        </Box>
                    </Box>
                </Box>
                <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                    <Box>
                        <Typography variant={'subtitle2'} >Keynote Speakers</Typography>
                    </Box>

                    {
                        data.keynote_speakers.map((speaker) => {
                            return (
                                <Box sx={{ display: "flex", gap: "1rem" }}>
                                    <Box sx={{ width: "15%" }}>
                                        <Card>
                                            <Box sx={{ borderBlockRadius: "10px", height: "200px" }}>
                                                <CardMedia
                                                    component="img"
                                                    sx={{ height: "100%" }}
                                                    image={Speaker1}
                                                    alt="Gen. Press Release"
                                                />
                                            </Box>
                                        </Card>
                                    </Box>
                                </Box>
                            );
                        })
                    }
                </Box>
            </Box>
            <Drawer
                anchor={"right"}
                open={state["right"]}
                onClose={toggleDrawer("right", false)}
                variant='temporary'
            >
                <Box
                    role="presentation"
                    sx={{
                        backgroundColor: "white",
                        height: "100%",
                        display: "flex",
                        // gap: "2rem",
                        flexDirection: "column",
                        width: 400
                    }}>
                    <Box sx={{
                        display: "flex", flexDirection: "column", justifyContent: "center",
                        alignItems: "center", backgroundColor: "#fff",
                        border: "2px dashed #000", padding: "4rem"
                    }}>
                        {FeaturedImage}
                        <Typography>Featured image</Typography>
                    </Box>

                    <Box
                        component="form"
                        sx={{
                            display: "flex",
                            flexDirection: "column",
                            paddingInline: "2rem",
                            paddingBlock: "1rem",
                            justifyContent: 'space-between',
                            height: "100%"
                        }}
                    >
                        <Box>
                            <Box
                                sx={{
                                    '& > :not(style)': { m: 1, width: '25ch' },
                                }}
                                noValidate
                                autoComplete="off"
                            >
                                <Typography>Keynote speaker's Name</Typography>
                                <TextField
                                    id="outlined-select-document"
                                    label="Enter title here"
                                    variant="standard" />
                            </Box>
                            <Box
                                sx={{
                                    '& > :not(style)': { m: 1, width: '25ch' },
                                }}
                                noValidate
                                autoComplete="off"
                            >
                                <Typography>Position</Typography>
                                <TextField
                                    id="outlined-select-document"
                                    label="rank here"
                                    variant="standard" />
                            </Box>
                            <Box
                                sx={{
                                    '& > :not(style)': { m: 1, width: '25ch' },
                                }}
                                noValidate
                                autoComplete="off"
                            >
                                <Typography>Description</Typography>
                                <TextField
                                    id="outlined-select-document"
                                    label="Enter description here"
                                    variant="standard" />
                            </Box>
                        </Box>

                        <Box sx={{ display: "flex", alignSelf: "flex-end", flexDirection: "column" }}>
                            <Button
                                variant="contained"
                                size='large'
                                // onClick={toggleDrawer("right", true)}
                                sx={{
                                    color: "#fff",
                                    fontSize: "12px",
                                    textTransform: "none",
                                    backgroundColor: "#000",
                                    paddingBlock: 0,
                                    cursor: "pointer",
                                    "&:hover": {
                                        // backgroundColor: "#000"
                                    }
                                }}
                            >Create</Button>
                        </Box>
                    </Box>
                </Box>
            </Drawer>
        </>
    )
}
