import React, { useState } from 'react'
import {
    Box,
    Button,
    Drawer,
    Typography,
    TextField
} from '@mui/material';
import { AddFile, UploadCertificate } from '../../assets';


export const UploadFiles = () => {
    const [state, setState] = useState("")

    const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        setState({ ...state, [anchor]: open });
    };

    return (
        <Box sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
        }}>
            <Drawer
                anchor={"right"}
                open={state["right"]}
                onClose={toggleDrawer("right", false)}
                variant='temporary'
            >
                <Box
                    role="presentation"
                    sx={{
                        backgroundColor: "white",
                        paddingInline: "2rem",
                        paddingBlock: "2rem",
                        height: "100%",
                        display: "flex", gap: "2rem",
                        flexDirection: "column",
                        width: 400,
                    }}>
                    <Box sx={{
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        boxShadow: "0px 52px 108px 32px rgba(0, 0, 0, 0.15)",
                        border: "2px dashed  rgba(0, 0, 0, 0.15)",
                        padding: "4rem",

                    }}>
                        {AddFile}
                    </Box>

                    <Box
                        component="form"
                        sx={{
                            display: "flex"
                        }}
                    >
                        <Box
                            sx={{
                                '& > :not(style)': { m: 1, width: '25ch' },
                            }}
                            noValidate
                            autoComplete="off"
                        >
                            <Typography>About Event</Typography>
                            <TextField
                                id="outlined-select-document"
                                select
                                label="select document"
                                variant="standard" />
                        </Box>
                    </Box>
                </Box>
            </Drawer>

            <Box sx={{
                display: "flex",
                justifyContent: "center",
                alignSelf: "center"
            }}>
                <Button
                    variant="contained"
                    size='medium'
                    startIcon={
                        <Box>
                            {UploadCertificate}
                        </Box>
                    }
                    onClick={toggleDrawer("right", true)}
                    sx={{
                        color: "#000",
                        fontSize: "12px",
                        textTransform: "none",
                        backgroundColor: "#EFEFEF",
                        paddingBlock: 0,
                        cursor: "pointer",
                        "&:hover": {
                            // backgroundColor: "#000"
                        }
                    }}
                >Click here to upload Files</Button>
            </Box>
        </Box>
    );
}