import { Button } from "./Button"
import { Box } from "@mui/material"
import { ArrowBackIos, ArrowForwardIos } from '@mui/icons-material';
import styled from "styled-components"

export const NavButton = styled.div`
    cursor: pointer;
    margin-inline-end: .3rem;
    border: 1px solid rgba(100, 100, 100, 0);
    background-color: transparent;
    font-weight: 600;

    :hover {
        color: rgba(100, 100, 100, 1);
    }
`

export const Paginate = (props) => {
    let { navigationList, current, lastPage, setCurrent } = props;

    function handlePageChange(page) {
        if (page < 0) {
            setCurrent(prev => (prev > 1) ? prev - 1 : prev)
        } else if (page === 0) {
            setCurrent(prev => (prev < lastPage) ? prev + 1 : prev)
        } else {
            setCurrent(prev => prev === page ? prev : page)
        }
    }

    const tableItems = navigationList?.map((item, int) => {
        const active = current === item ? "active" : ''
        return (
            <Button
                active={active}
                item={item}
                index={int}
                handlePageChange={handlePageChange}
                key={int}
            />
        )
    })

    return (
        <Box sx={{
            display: "flex",
            gap: "1rem",
            margin: "0 auto",
            marginBlock: "2rem",
            alignItems: "center"
        }}>
            <NavButton onClick={() => handlePageChange(-1)}>
                <ArrowBackIos />
            </NavButton>
            <Box sx={{
                display: "flex",
                gap: "23px"
            }}>
                {tableItems}
            </Box>
            <NavButton onClick={() => handlePageChange(0)}>
                <ArrowForwardIos />
            </NavButton>
        </Box >
    )
}