import React from 'react'
import {
    Box,
    Button,
    Typography,
    Grid,
    Stack,
    Card,
    CardContent,
    CardMedia
} from '@mui/material';
import { Edit, Upload } from '../../assets';
import { usePressReleasesApi } from '../../hooks/setLocalStorage';


export const PressRelease = ({ togglePressDrawer, toggleEditDrawer, releasesProp }) => {
    if (typeof (releasesProp) === "object")
        return (
            <Box sx={{ display: "flex", flexDirection: "column", padding: "1.5rem", gap: "1rem", overflow: "scroll" }}>
                <Box>
                    <Box sx={{
                        display: "flex",
                        justifyContent: "space-between",
                        flexDirection: "column",
                        gap: "1rem",
                        height: "20%",
                    }}>
                        <Box sx={{ display: "flex", justifyContent: "space-between" }}>

                            <Box sx={{ display: "flex", flexDirection: "column" }}>
                                <Typography>Press Release</Typography>
                                <Typography fontSize={8}>{releasesProp.length} Release</Typography>
                            </Box>

                            {/* <Typography fontSize={12}>View all</Typography> */}
                            <Stack>
                                <Button
                                    onClick={togglePressDrawer("right", true)}
                                    variant="outlined"
                                    size="small"
                                    startIcon={
                                        <Box>
                                            {Upload}
                                        </Box>
                                    }
                                    sx={{ border: "1px solid black", color: "#000", fontSize: "8px" }}>Upload Press Release</Button>
                            </Stack>
                        </Box>
                    </Box>
                </Box>

                <Box>
                    <Grid container
                        rowSpacing={{ xs: 1, sm: 1, md: 2 }}
                        columnSpacing={{ xs: 1, sm: 1, md: 2 }}
                    >
                        {releasesProp.map((event, event_key) => {
                            const date = new Date(event.date).toDateString()
                            return (
                                <Grid item xs={6} key={event.id}>
                                    <Card sx={{ display: 'flex', height: 150 }}>
                                        <Box sx={{ width: "40%" }}>
                                            <CardMedia
                                                component="img"
                                                sx={{ height: "100%" }}
                                                image={event.image}
                                                alt="Gen. Press Release"
                                            />
                                        </Box>
                                        <Box sx={{ display: 'flex', flexDirection: 'column', flexGrow: 1 }}>
                                            <CardContent sx={{ flex: '1 0 auto', paddingBlock: "0.5rem" }}>
                                                <Typography fontWeight={500}>{event.title}</Typography>
                                            </CardContent>
                                            <CardContent sx={{ display: 'flex', justifyContent: "space-between", alignItems: 'center', pl: 2, paddingBlock: "0.1rem" }}>
                                                <Typography variant="subtitle1" color="text.secondary" fontSize={12} component="div">
                                                    {date}
                                                </Typography>
                                                <Stack>
                                                    <Button
                                                        variant="contained"
                                                        size="small"
                                                        startIcon={
                                                            <Box>
                                                                {Edit}
                                                            </Box>
                                                        }
                                                        onClick={toggleEditDrawer("right", true, event.id)}
                                                        sx={{
                                                            color: "#fff",
                                                            fontSize: "8px",
                                                            textTransform: "none",
                                                            backgroundColor: "#1C274C",
                                                            paddingBlock: 0,
                                                            "&:hover": {
                                                                backgroundColor: "#000"
                                                            }
                                                        }}
                                                    >Edit</Button>
                                                </Stack>
                                            </CardContent>
                                        </Box>

                                    </Card>
                                </Grid>
                            )
                        })}
                    </Grid>
                </Box>
            </Box >
        )

}
