let Auth = (() => {
  let bearer = {
    local: `Bearer ${localStorage.getItem("tokenn")}`,
    static: credentials.token
  }

  if (localStorage.getItem("tokenn"))
    return bearer.local
  else {
    return bearer.static
  }
})()


export const useLoginApi = () => {
  const [isLoading, setLoading] = useState(false);
  const [apiData, setApiData] = useState({});
  const [apiMessage, setApiMessage] = useState("");

  useEffect(() => {
    async function fetchCredentials() {
      try {
        let data = qs.stringify({
          'client_id': 'NAFESuperAdminClient',
          'client_secret': 'f7fc5d2f2f2944009117a78ed54b2ea2@@z5h25@p0',
          'grant_type': 'password',
          'username': 'gadam@nahq.com',
          'password': '#Passw0rd!',
          'scope': 'openid profile offline_access'
        });

        let config = {
          method: 'post',
          maxBodyLength: Infinity,
          url: `${credentials.baseUrl}/o/oauth/token`,
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
          data: data
        };

        setLoading(true);

        axios.request(config)
          .then((response) => {
            const { data, message } = response.data;

            setApiData(data);
            setApiMessage(message);

            localStorage.setItem('tokenn', data.access_token);

            setLoading(false);
          })
          .catch((error) => {
            console.log(error);
            setLoading(false);
          });

      } catch (error) {
        console.clear()
        console.error({ error })
      }

    }

    fetchCredentials();
  }, [])

  return { isLoading, apiData }
}


export const usePaginationApi = () => {
  const [paginationData, setPaginationData] = useState({});
  const [paginationMessage, setPaginationMessage] = useState("");
  const [page, setPage] = useState(1);
  const [currentPage, setCurrentPage] = useState(1);
  const [tenantId, setTenantId] = useState("tenant_656b3b73b18e2c08686346bd");
  const [tenantDetails, setTenantDetails] = useState({});
  const [tenantOngoing, setTenantOngoing] = useState({});
  const [tenantUpcoming, setTenantUpcoming] = useState({});
  const [webinarCount, setWebinarCount] = useState(0);
  const [selectTenant, setSelectTenant] = useState("tenant_656b3b73b18e2c08686346bd");
  const [trigger, setTrigger] = useState(false);


  useEffect(() => {

    async function fetchLists() {
      try {
        let data = '';
        let config = {
          method: 'get',
          maxBodyLength: Infinity,
          url: `${credentials.baseUrl}/tenants/sa/`,
          headers: {
            'Authorization': Auth
          },
          data: data
        };

        axios.request(config)
          .then((response) => {
            const { message, data } = response.data;

            setPaginationData(data);
            setPaginationMessage(message);
            setWebinarCount(() => {
              let count = 0;

              data.results.map(tenant => {
                count += tenant.webinars
              })

              return count;
            })
          })
          .catch((error) => {
            console.log(error);
          });

      } catch (error) {
        console.clear()
        console.error({ error })
      }

    }


    async function populateTenant(tenant_identification) {
      try {
        let data = '';
        let config = {
          method: 'get',
          maxBodyLength: Infinity,
          url: `${credentials.baseUrl}/tenants/sa/${tenant_identification}/details`,
          headers: {
            'Authorization': Auth
          },
          data: data
        };

        // setLoading(true);
        // console.log({ token: localStorage.getItem("token") })

        axios.request(config)
          .then((response) => {
            const { message, data } = response.data;

            setTenantDetails({ [tenant_identification]: data.members });
            setTenantOngoing({ [tenant_identification]: data.ongoing_events });
            setTenantUpcoming({ [tenant_identification]: data.upcoming_events });
            // console.log(JSON.stringify(response.data));
          })
          .catch((error) => {
            console.log(error);
          });

      } catch (error) {
        console.clear()
        console.error({ error })
      }

    }

    fetchLists();
    // populateTenant(tenantId);
  }, [trigger])

  return {
    page,
    setPage,
    paginationData,
    current: currentPage, setCurrentPage,
    tenantDetails,
    tenantOngoing,
    tenantUpcoming,
    tenantId,
    webinarCount,
    setTrigger
  }
}

