export const usePaginationUserandDepartmentApi = (page = 1) => {
  const [current, setCurrent] = useState(page);
  const [itemNumber, setItemNumber] = useState(10);
  const [userList, setUserList] = useState([]);
  const [lastPage, setLastPage] = useState(1);

  const limit = 10;
  const dept_limit = 10;

  const [currentPage, setCurrentPage] = useState(1);
  const [itemNumberDept, setItemNumberDept] = useState(10);
  const [departmentList, setDepartmentList] = useState([]);
  const [lastPageDept, setLastPageDept] = useState(1);

  const navigationListDept = Array(Math.ceil(itemNumberDept / dept_limit)).fill(null).map((_, i) => i += 1);
  const navigationList = Array(Math.ceil(itemNumber / limit)).fill(null).map((_, i) => i += 1);

  async function fetchUsers() {
    console.log("iran")
    const url = `${credentials.baseUrl}/users/sa/?page=${current}&items_per_page=${limit}`;
    try {
      let data = '';
      let config = {
        method: 'get',
        maxBodyLength: Infinity,
        url,
        headers: {
          'Authorization': Auth
        },
        data: data
      };

      axios.request(config)
        .then((response) => {
          const { message, data } = response.data;
          setItemNumber(data.number_of_items);
          setUserList(data.results);
          setLastPage(data.number_of_pages);
        })
        .catch((error) => {
          console.log(error);
        });

    } catch (error) {
      console.clear()
      console.error({ error })
    }

  };

  useEffect(() => {
    fetchUsers();
  }, [current])

  async function fetchTenants() {
    const dept_url = `${credentials.baseUrl}/tenants/sa/?page=${currentPage}&items_per_page=${dept_limit}`;
    try {
      let data = '';
      let config = {
        method: 'get',
        maxBodyLength: Infinity,
        url: dept_url,
        headers: {
          'Authorization': Auth
        },
        data: data
      };

      axios.request(config)
        .then((response) => {
          const { message, data } = response.data;
          setItemNumberDept(data.number_of_items);
          setDepartmentList(data.results);
          setLastPageDept(data.number_of_pages);
        })
        .catch((error) => {
          console.log(error);
        });

    } catch (error) {
      console.clear()
      console.error({ error })
    }

  };

  useEffect(() => {
    fetchTenants();
  }, [currentPage])

  return {
    current,
    setCurrent,
    navigationList,
    lastPage,
    userList,

    currentPage,
    setCurrentPage,
    departmentList,
    lastPageDept,
    navigationListDept
  };
}

