export const usePressReleasesApi = () => {

  const [releases, setReleases] = useState([]);
  const [editId, setEditId] = useState(null);
  const [trigger, setTrigger] = useState(false);
  const [data, setData] = useState({
    "title": "",
    "image": "",
    "files": [
      "https://www.nafedev.site/v1/files/view/file_65ac43c509924f1c1198dc48",
      "http://www.nafedev.site/v1/files/view/file_65ae0835b88318d3538ebe5e"
    ],
    "date": "2024-01-25",
    "description": ""
  });

  const [editData, setEditData] = useState({});


  async function fetchReleases() {
    try {
      let data = "";
      let config = {
        method: 'get',
        maxBodyLength: Infinity,
        url: `${credentials.baseUrl}/press-releases/sa/`,
        headers: {
          'Authorization': Auth
        },
        data
      };

      let dataResponse = [];

      axios.request(config)
        .then((response) => {
          const { message, data } = response.data;
          setReleases(data.results)
          dataResponse = data.results;
        })
        .catch((error) => {
          console.log(error);
        });

      return dataResponse;

    } catch (error) {
      console.clear()
      console.error({ error })
    };
  }

  async function addRelease(data, setPressState) {
    const parse = JSON.stringify(data);
    try {
      // let data = JSON.stringify(data);
      let config = {
        method: 'post',
        maxBodyLength: Infinity,
        url: `${credentials.baseUrl}/press-releases/sa/add`,
        headers: {
          'Content-Type': 'application/json',
          'Authorization': Auth
        },
        data: parse
      };

      axios.request(config)
        .then((response) => {
          const { message, data } = response.data;
          setTrigger(prev => !prev);
          setPressState("right", false);
        })
        .catch((error) => {
          console.log(error);
        });


    } catch (error) {
      console.clear()
      console.error({ error })
    };
  }

  async function updateReleaseWithId(event, { raw_data, id, closeEditModal }) {
    try {
      let data = JSON.stringify({
        "title": "Demo Press ",
        "image": "http://www.nafedev.site/v1/files/view/file_65af8229c8851756b80e0915",
        "files": [
          "https://www.nafedev.site/v1/files/view/file_65ac43c509924f1c1198dc48",
          "https://www.nafedev.site/v1/files/view/file_65adaae02c5326a12ad07000"
        ],
        "date": "2024-01-28",
        "description": "Lorem ipsum description",
        ...raw_data
      });

      let config = {
        method: 'put',
        maxBodyLength: Infinity,
        url: `${credentials.baseUrl}/press-releases/sa/${id}/edit`,
        headers: {
          'Content-Type': 'application/json',
          'Authorization': Auth
        },
        data
      };

      axios.request(config)
        .then((response) => {
          const { message, data } = response.data;
          setTrigger(prev => !prev);
          closeEditModal(event);
        })
        .catch((error) => {
          console.log(error);
        });


    } catch (error) {
      console.clear()
      console.error({ error })
    };
  }

  function fetchReleasesWithId() {
    let i = 0;
    if (!editId) return
    while (i < releases.length) {
      if (releases[i].id === editId) {
        break;
      }

      i++;
    };

    setEditData(releases[i]);
  }

  useEffect(() => {
    fetchReleases();
  }, [trigger])

  useEffect(() => {
    fetchReleasesWithId();
  }, [editId])

  return {
    data,
    setData,

    releases, setReleases,

    editId, setEditId,

    addRelease,
    editData, setEditData,

    fetchReleases,
    updateReleaseWithId
  };
};