import React from 'react'
import styled from "styled-components"

export const PageButton = styled.button`
display: inline;
background: none;
border: 1px solid rgba(110, 110, 110, .3);
padding-block: 5px;
padding-inline: 10px;
// min-height: 25px;
border-radius: 50%;
margin-inline-end: .3rem;
cursor: pointer;

&::hover {
    background-color: #000;
    color: white;
}

&[data-active="active"]{
    background-color: rgba(0, 0, 0, .5);
    color: white;
    cursor: default;
}
`

export const Button = (props) => {
    const { item, active, handlePageChange, index } = props;
    return (
        <PageButton
            onClick={() => handlePageChange(item)}
            key={index}
            data-active={active}
        >
            {item}
        </PageButton>
    );
}