import React from 'react'
import {
    Box,
    Button,
    Typography,
    Grid,
    Stack,
} from '@mui/material';
import { Edit } from '../../assets';
import { useFetchListApi } from '../../hooks/setLocalStorage';

export const LiveEvents = ({ doc }) => {

    const { events } = useFetchListApi();


    const header = <Box
        style={{
            ...styles.departmentContainer,
            borderBlockEnd: "1px solid #363636"
        }}>
        <Grid container spacing={2}>
            <Grid item xs={6}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Event Title</Typography>
                </Box>
            </Grid>

            <Grid item xs={2}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap >Event type</Typography>
                </Box>
            </Grid>

            <Grid item xs={4}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Date</Typography>
                </Box>
            </Grid>

        </Grid>
    </Box>

    return (
        <Box sx={{
            width: "100%",
            display: "flex",
            flexDirection: "column",
            gap: "1rem",
        }}>
            <Box>
                {header}
                {events.map((node, y) => {
                    const date = new Date(node.start_date).toDateString();
                    return (
                        <React.Fragment key={node.id}>
                            <Box style={y % 2 === 0 ? { ...styles.departmentContainer } : { ...styles.departmentContainer, backgroundColor: "#fff" }}>
                                <Grid container spacing={2}>
                                    <Grid item xs={6}>
                                        <Box sx={{ display: "flex", flexDirection: "row", alignItems: "center", gap: "1rem" }}>
                                            <Typography variant='subtitle2'>{node.title}</Typography>
                                        </Box>
                                    </Grid>

                                    <Grid item xs={2}>
                                        <Box sx={{ display: "flex", alignItems: "center" }}>
                                            <Typography fontSize={14} fontWeight={500} >
                                                {node.type}
                                            </Typography>
                                        </Box>
                                    </Grid>

                                    <Grid item xs={4}>
                                        <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                                            <Box>
                                                <Typography fontSize={14}>{date}</Typography>
                                            </Box>

                                            <Stack>
                                                <Button
                                                    variant="contained"
                                                    size="small"
                                                    startIcon={
                                                        <Box>
                                                            {Edit}
                                                        </Box>
                                                    }
                                                    // onClick={toggleEditDrawer("right", true)} 
                                                    sx={{
                                                        color: "#fff",
                                                        fontSize: "8px",
                                                        textTransform: "none",
                                                        backgroundColor: "#1C274C",
                                                        paddingBlock: 0,
                                                        "&:hover": {
                                                            backgroundColor: "#000"
                                                        }
                                                    }}
                                                >Edit</Button>
                                            </Stack>
                                        </Box>
                                    </Grid>
                                </Grid>
                            </Box>
                        </React.Fragment>
                    );
                })}
            </Box>
        </Box >
    );
}

const styles = {
    departmentContainer: {
        display: "flex",
        alignItems: "center",
        gap: "1rem",
        // borderBlockEnd: "1px solid #E5E5E5"
        backgroundColor: "#FAFAFA",
        width: "100%",
        justifyContent: "center",
        paddingBlock: "1rem",
        paddingInline: "1rem"
    },

    textContainer: {
        display: "flex",
        gap: ".5rem",
        flexDirection: "column",
    },
    liveButton: {
        color: "#fff",
        backgroundColor: "#EC1300",
        // backgroundColor: "#C0C0C0",
        borderRadius: "10px",
        fontSize: "8px"
    }
}