import React, { useState } from 'react';
import { Avatar, Box, Button, Drawer, Stack, Typography, Grid } from "@mui/material";
import { Paginate } from '../../components/Paginate';
import { SearchIcon, Edit, NAFE } from '../../assets';
// import { DepartmentDetails } from '../components/DepartmentDetails';
import { ArrowBackIos } from '@mui/icons-material';
import { usePaginationApi, useUserListApi } from '../../hooks/setLocalStorage';

export const MemberList = (props) => {
    const { tenantDetails, tenantId } = usePaginationApi();
    const { users, profile, setUserId, setProfile } = useUserListApi();
    const [state, setState] = useState("");
    const [user, setUser] = useState({});

    const toggleDrawer = (anchor, open, member,) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        setState({ ...state, [anchor]: open });

        if (!member) return
        setUser({
            ...member,
            department: "NAEME",
            gender: "Male"
        });
    };

    const header = <Box
        style={{
            ...styles.departmentContainer,
            borderBlockEnd: "1px solid #363636"
        }}>
        <Grid container spacing={2}>
            <Grid item xs={3}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Name</Typography>
                </Box>
            </Grid>

            <Grid item xs={3}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Gender</Typography>
                </Box>
            </Grid>

            <Grid item xs={3}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Phone</Typography>
                </Box>
            </Grid>

            <Grid item xs={3}>
                <Box sx={{ display: "flex", gap: '.5rem' }}>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Email</Typography>
                </Box>
            </Grid>
        </Grid>
    </Box>

    const UserList = (props) => {
        const { members } = props;
        if (!members) return;
        return members.map(member => (
            <Box style={{ ...styles.departmentContainer }} onClick={toggleDrawer("right", true, member)} key={members.id}>
                <Grid container spacing={2}>
                    <Grid item xs={3}>
                        <Box sx={{ display: "flex", flexDirection: "row", alignItems: "center", gap: "1rem" }}>
                            <Avatar alt="Remy Sharp" src="" sx={{ width: 35, height: 35 }} />
                            <Typography variant='subtitle2'>{member.full_name}</Typography>
                        </Box>
                    </Grid>

                    <Grid item xs={3}>
                        <Box>
                            <Typography fontSize={14} fontWeight={500} sx={{ alignSelf: "center" }}>
                                {member.gender || "Male"}
                            </Typography>
                        </Box>
                    </Grid>

                    <Grid item xs={3}>
                        <Box>
                            <Typography>{member.phone_number}</Typography>
                        </Box>
                    </Grid>

                    <Grid item xs={3}>
                        <Box sx={{ display: "flex", gap: '.5rem' }}>
                            <Typography>{member.email}</Typography>
                            {/* <Button style={!doc ? styles.liveButton : { ...styles.liveButton, backgroundColor: "#cacaca" }}>Live</Button> */}
                        </Box>
                    </Grid>
                </Grid>
            </Box>
        ));
    }

    return (
        <>
            <Grid item
                sx={{
                    height: "100vh",
                }}
            >
                <Box
                    sx={{
                        padding: "2rem",
                        minHeight: "100vh",
                        paddingBlock: " 2rem",
                        paddingInline: "3rem",
                        display: "flex",
                        flexDirection: "column",
                        gap: "1rem"
                    }}
                >
                    <Box sx={{
                        display: "flex",
                        justifyContent: "space-between",
                        flexDirection: "column",
                        gap: "1rem",
                        height: "20%",
                    }}>
                        <Box sx={{ display: "flex", justifyContent: "space-between" }}>

                            <Box sx={{ display: "flex", flexDirection: "column" }}>
                                <Typography>All Users</Typography>
                                <Typography fontSize={10} >{tenantDetails[tenantId]?.number_of_items} Members</Typography>
                            </Box>

                            <Box sx={{
                                dislay: "flex",
                                alignSelf: "flex-end",
                                borderRadius: "10px",
                                width: "25%"
                            }}>

                                <Box sx={{
                                    backgroundColor: "#f2f2f2",
                                    borderRadius: "20px",
                                    width: "100%",
                                    display: "flex",
                                    alignItems: "center",
                                    justifyContent: "space-between",
                                    padding: '.5rem',
                                    // border: "1px solid black"
                                }}>
                                    <Typography color={"#C8C8C8"} fontSize={12}>Search...</Typography>

                                    <Box sx={{ display: "flex", alignItems: "center" }}>
                                        {/* <SearchIcon htmlColor='#797979' /> */}
                                        {SearchIcon}
                                    </Box>
                                </Box>
                            </Box>
                        </Box>
                    </Box>

                    <Box
                        sx={{
                            width: "100%",
                            minHeight: "50vh",
                            gridTemplateColumns: "repeat(10, 10%)",
                        }}
                    >
                        <Box>
                            {header}
                            {console.log({ users, tenantDetails })}
                            <UserList
                                // members={tenantDetails[tenantId]}
                                members={users}
                                tenant={""}
                            />

                        </Box>
                    </Box>

                    <Box sx={{ alignSelf: "flex-end" }}>
                        <Paginate pageList={[1, 2, 3, 4]} current={1} setCurrent={() => { }} />
                    </Box>
                </Box >
            </Grid>
            <Drawer
                anchor={"right"}
                open={state["right"]}
                onClose={toggleDrawer("right", false)}
                variant='temporary'
            >
                <Box
                    role="presentation"
                    sx={{
                        paddingInline: "1rem",
                        paddingBlock: "2rem",
                        height: "100%",
                        display: "flex", gap: "2rem",
                        flexDirection: "column",
                        width: 350
                    }}>
                    <Box sx={{ display: "flex", justifyContent: "space-between", width: "100%" }}>
                        <Box sx={{ display: "flex", justifyContent: "space-between", width: "100%" }}>
                            <Box sx={{ display: "flex", alignItems: "center" }}>
                                <ArrowBackIos />
                                <Typography fontSize={12}>Back</Typography>
                            </Box>
                            <Box sx={{ display: "flex", gap: ".5rem" }}>
                                <Box sx={{
                                    // width: "10%",
                                }}>
                                    <Stack>
                                        <Button
                                            variant="contained"
                                            size='medium'
                                            startIcon={
                                                <Box>
                                                    {Edit}
                                                </Box>
                                            }
                                            sx={{
                                                color: "#fff",
                                                fontSize: "12px",
                                                textTransform: "none",
                                                backgroundColor: "#000",
                                                paddingBlock: 0,
                                                "&:hover": {
                                                    backgroundColor: "#000"
                                                }
                                            }}
                                        >Edit</Button>
                                    </Stack>
                                </Box>
                            </Box>
                        </Box>
                    </Box>

                    <Box sx={{ display: "flex", width: "100%", justifyContent: "center" }}>
                        <Avatar src={NAFE} sx={{ width: 150, height: 150 }} />
                    </Box>

                    <Box sx={{ display: "flex", gap: "1.5rem", flexDirection: "column", padding: "1rem" }}>
                        <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                            <Typography color="#5B5B5B" fontSize={16}>Full Name</Typography>
                            <Typography>{user.full_name}</Typography>
                        </Box>
                        <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                            <Typography color="#5B5B5B" fontSize={16}>Rank</Typography>
                            <Typography>{user.rank}</Typography>
                        </Box>
                        <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                            <Typography color="#5B5B5B" fontSize={16}>Department</Typography>
                            <Typography>{user.department || "NAEME"}</Typography>
                        </Box>
                        <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                            <Typography color="#5B5B5B" fontSize={16}>Email</Typography>
                            <Typography>{user.email}</Typography>
                        </Box>

                        <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                            <Typography color="#5B5B5B" fontSize={16}>Phone Number</Typography>
                            <Typography>{user.phone_number}</Typography>
                        </Box>
                    </Box>
                </Box>
            </Drawer>
        </>
    );
};


const styles = {
    departmentContainer: {
        display: "flex",
        alignItems: "center",
        gap: "1rem",
        // borderBlockEnd: "1px solid #E5E5E5"
        backgroundColor: "#FAFAFA",
        width: "100%",
        justifyContent: "center",
        paddingBlock: "1rem",
        paddingInline: "1rem",
        cursor: "pointer"
    },

    textContainer: {
        display: "flex",
        gap: ".5rem",
        flexDirection: "column",
    },
    liveButton: {
        color: "#fff",
        backgroundColor: "#EC1300",
        // backgroundColor: "#C0C0C0",
        borderRadius: "10px",
        fontSize: "8px"
    }
}
