import React from 'react'
import {
    Box,
    Button,
    Avatar,
    Typography,
    AvatarGroup,
    Grid,
} from '@mui/material'
import { Link } from 'react-router-dom';

export const DepartmentList = ({ doc, title, openModal, data }) => {
    if (!data) return;
    const {
        tenant_id,
        name,
        code,
        total_events,
        total_members,
        webinars
    } = data;

    const header = <Box style={{ ...styles.departmentContainer, backgroundColor: "#fff" }}>
        <Grid item spacing={2} >
            <Grid item xs={2}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Departments</Typography>
                </Box>
            </Grid>

            <Grid item xs={2}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap >No. Of Users</Typography>
                </Box>
            </Grid>

            <Grid item xs={2}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Events</Typography>
                </Box>
            </Grid>

            <Grid item xs={6}>
                <Box sx={{ display: "flex", gap: '.5rem' }}>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Live Webinars</Typography>
                </Box>
            </Grid>
        </Grid>
    </Box >

    const body = (
        <Grid item spacing={2}>
            <Link to='/departments' style={{ textDecoration: "none", color: "#000" }}>
                <Box
                    // onClick={openModal}
                    style={!doc ? { ...styles.departmentContainer } : { ...styles.departmentContainer, backgroundColor: "#fff" }}
                    key={tenant_id}
                >
                    <Grid container spacing={2}>
                        <Grid item xs={2}>
                            <Box>
                                <Typography variant='subtitle2'>{code}</Typography>
                            </Box>
                        </Grid>

                        <Grid item xs={2}>
                            <Box sx={{ display: "flex", flexDirection: "row", }}>
                                <AvatarGroup max={3} sx={{ flexDirection: "row-reverse" }}>
                                    <Avatar alt="Remy Sharp" src="" sx={{ width: 20, height: 20 }} />
                                    <Avatar alt="Remy Sharp" src="" sx={{ width: 20, height: 20 }} />
                                    <Avatar alt="Remy Sharp" src="" sx={{ width: 20, height: 20 }} />
                                </AvatarGroup>

                                <Typography fontSize={10} sx={{ alignSelf: "center" }}>
                                    {total_events}
                                </Typography>
                            </Box>
                        </Grid>

                        <Grid item xs={2}>
                            <Box>
                                <Typography>{total_members}</Typography>
                            </Box>
                        </Grid>

                        <Grid item xs={6}>
                            <Box sx={{ display: "flex", gap: '.5rem' }}>
                                <Typography>{webinars}</Typography>
                                <Button style={!doc ? styles.liveButton : { ...styles.liveButton, backgroundColor: "#cacaca" }}>Live</Button>
                            </Box>
                        </Grid>
                    </Grid>
                </Box>
            </Link>
        </Grid>
    )

    return title ? header : body
};

const styles = {
    departmentContainer: {
        display: "flex",
        alignItems: "center",
        gap: "1rem",
        // borderBlockEnd: "1px solid #E5E5E5"
        backgroundColor: "#FAFAFA",
        width: "100%",
        justifyContent: "center",
        paddingBlock: ".5rem",
        // paddingInline: "1rem"
    },

    textContainer: {
        display: "flex",
        gap: ".5rem",
        flexDirection: "column",
    },
    liveButton: {
        color: "#fff",
        backgroundColor: "#EC1300",
        // backgroundColor: "#C0C0C0",
        borderRadius: "10px",
        fontSize: "8px"
    }
}
