import React, { useState } from 'react';
import { Avatar, Box, Button, Drawer, Stack, Typography, Grid } from "@mui/material";
import { Paginate } from '../components/Paginate';
import { SearchIcon, Edit, NAFE } from '../assets';
import { DepartmentDetails } from '../components/DepartmentDetails';
import { ArrowBackIos } from '@mui/icons-material';
import { useUserListApi, usePaginationUserandDepartmentApi } from '../hooks/setLocalStorage';



export const Users = (props) => {
    const { users, profile, setUserId, setProfile } = useUserListApi();
    const {
        userList,
        navigationList,
        current,
        setCurrent,
        lastPage
    } = usePaginationUserandDepartmentApi();

    const [state, setState] = useState("");

    const ProfileDto = {
        "id": "",
        "gender": "",
        "email": "",
        "phone_number": "",
        "rank": "",
        "department_name": "",
        "full_name": ""
    };

    const toggleDrawer = (anchor, open, id) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        setState({ ...state, [anchor]: open });
        // console.log({ id, open })
        open ? setUserId(id) : setProfile(ProfileDto);
    };


    const header = <Box
        style={{
            ...styles.departmentContainer,
            borderBlockEnd: "1px solid #363636",
            borderRadius: 0
        }}>
        <Grid container spacing={2}>
            <Grid item xs={3}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Name</Typography>
                </Box>
            </Grid>

            <Grid item xs={3}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap >Departments</Typography>
                </Box>
            </Grid>

            <Grid item xs={3}>
                <Box>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Rank</Typography>
                </Box>
            </Grid>

            <Grid item xs={3}>
                <Box sx={{ display: "flex", gap: '.5rem' }}>
                    <Typography sx={{ fontSize: "16px", fontWeight: 500 }} noWrap>Email</Typography>
                </Box>
            </Grid>
        </Grid>
    </Box>

    return (
        <Grid item xs={10}>
            <Box sx={{ backgroundColor: "#fff", height: "100vh", width: "100%" }}>
                <Grid item xs={10}>
                    <Box
                        sx={{
                            padding: "2rem",
                            backgroundColor: "white",
                            paddingBlock: " 2rem",
                            paddingInline: "3rem",
                            display: "flex",
                            flexDirection: "column",
                            gap: "1rem"
                        }}
                    >
                        <Box sx={{
                            overflowY: "scroll"
                        }}>
                            <Box sx={{
                                display: "flex",
                                justifyContent: "space-between",
                                flexDirection: "column",
                                gap: "1rem",
                                height: "20%",
                            }}>
                                <Box sx={{ display: "flex", justifyContent: "space-between" }}>

                                    <Box sx={{ display: "flex", flexDirection: "column" }}>
                                        <Typography>All Users</Typography>
                                        <Typography fontSize={10}>{users.length} Users</Typography>
                                    </Box>

                                    <Box sx={{
                                        dislay: "flex",
                                        alignSelf: "flex-end",
                                        borderRadius: "10px",
                                        width: "25%"
                                    }}>

                                        {false && <Box sx={{
                                            backgroundColor: "#f2f2f2",
                                            borderRadius: "20px",
                                            width: "100%",
                                            display: "flex",
                                            alignItems: "center",
                                            justifyContent: "space-between",
                                            padding: '.5rem',
                                            // border: "1px solid black"
                                        }}>
                                            <Typography color={"#C8C8C8"} fontSize={12}>Search...</Typography>

                                            <Box sx={{ display: "flex", alignItems: "center" }}>
                                                {/* <SearchIcon htmlColor='#797979' /> */}
                                                {SearchIcon}
                                            </Box>
                                        </Box>}
                                    </Box>
                                </Box>
                            </Box>

                            <Box
                                sx={{
                                    width: "100%",
                                    minHeight: "50vh",
                                    gridTemplateColumns: "repeat(10, 10%)",
                                }}
                            >
                                <Box>

                                    {header}
                                    <Box
                                        style={{ display: "flex", flexDirection: "column", gap: 2, paddingBlockStart: 1 }}
                                    >
                                        {userList.map((user) => {
                                            return (
                                                <Box
                                                    style={
                                                        { ...styles.departmentContainer }
                                                        // backgroundColor: "#fff", borderRadius: "8px" 
                                                    }
                                                    onClick={toggleDrawer("right", true, user.id)}
                                                    key={user.id}
                                                >
                                                    <Grid container spacing={2}>
                                                        <Grid item xs={3}>
                                                            <Box sx={{ display: "flex", flexDirection: "row", alignItems: "center", gap: "1rem" }}>
                                                                <Avatar alt="Remy Sharp" src="" sx={{
                                                                    width: 35, height: 35, fill: "#fff",
                                                                    border: "1px solid red",
                                                                    backgroundColor: "rgba(28, 39, 76)"
                                                                }} />
                                                                <Typography variant='subtitle2'>{user.full_name}</Typography>
                                                            </Box>
                                                        </Grid>

                                                        <Grid item xs={3}>
                                                            <Box>
                                                                <Typography fontSize={14} fontWeight={500} sx={{ alignSelf: "center" }}>
                                                                    {user.department_name}
                                                                </Typography>
                                                            </Box>
                                                        </Grid>

                                                        <Grid item xs={3}>
                                                            <Box>
                                                                <Typography>{user.rank || <em>N/A</em>}</Typography>
                                                            </Box>
                                                        </Grid>

                                                        <Grid item xs={3}>
                                                            <Box sx={{ display: "flex", gap: '.5rem' }}>
                                                                <Typography>{user.email}</Typography>
                                                                {/* <Button style={!doc ? styles.liveButton : { ...styles.liveButton, backgroundColor: "#cacaca" }}>Live</Button> */}
                                                            </Box>
                                                        </Grid>
                                                    </Grid>
                                                </Box>
                                            )
                                        })}
                                    </Box>
                                </Box>
                            </Box>
                        </Box>

                        <Box sx={{ alignSelf: "flex-end" }}>
                            <Paginate
                                navigationList={navigationList}
                                current={current}
                                setCurrent={setCurrent}
                                lastPage={lastPage}
                            />
                        </Box>
                    </Box >
                </Grid>
                <Drawer
                    anchor={"right"}
                    open={state["right"]}
                    onClose={toggleDrawer("right", false)}
                    variant='temporary'
                >
                    <Box
                        role="presentation"
                        sx={{
                            paddingInline: "1rem",
                            paddingBlock: "2rem",
                            height: "100%",
                            display: "flex", gap: "2rem",
                            flexDirection: "column",
                            width: 350
                        }}>
                        <Box sx={{ display: "flex", justifyContent: "space-between", width: "100%" }}>
                            <Box sx={{ display: "flex", justifyContent: "space-between", width: "100%" }}>
                                <Box sx={{ display: "flex", alignItems: "center" }}>
                                    <ArrowBackIos />
                                    <Typography fontSize={12}>Back</Typography>
                                </Box>
                                <Box sx={{ display: "flex", gap: ".5rem" }}>
                                    <Box sx={{
                                        // width: "10%",
                                    }}>
                                        <Stack>
                                            <Button
                                                variant="contained"
                                                size='medium'
                                                startIcon={
                                                    <Box>
                                                        {Edit}
                                                    </Box>
                                                }
                                                sx={{
                                                    color: "#fff",
                                                    fontSize: "12px",
                                                    textTransform: "none",
                                                    backgroundColor: "#1C274C",
                                                    paddingBlock: 0,
                                                    "&:hover": {
                                                        backgroundColor: "#7C7C7C"
                                                    }
                                                }}
                                            >Edit</Button>
                                        </Stack>
                                    </Box>
                                </Box>
                            </Box>
                        </Box>

                        <Box sx={{ display: "flex", width: "100%", justifyContent: "center" }}>
                            <Avatar src={NAFE} sx={{ width: 150, height: 150 }} />
                        </Box>

                        <Box sx={{ display: "flex", gap: "1.5rem", flexDirection: "column", padding: "1rem" }}>
                            <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                                <Typography color="#5B5B5B" fontSize={16}>Full Name</Typography>
                                <Typography>{profile.full_name}</Typography>
                            </Box>
                            <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                                <Typography color="#5B5B5B" fontSize={16}>Rank</Typography>
                                <Typography>
                                    {profile.rank === "" ? "N/A" : profile.rank}
                                </Typography>
                            </Box>
                            <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                                <Typography color="#5B5B5B" fontSize={16}>Department</Typography>
                                <Typography>{profile.department_name}</Typography>
                            </Box>
                            <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                                <Typography color="#5B5B5B" fontSize={16}>Email</Typography>
                                <Typography><em>{profile.email}</em></Typography>
                            </Box>

                            <Box sx={{ display: "flex", gap: "0.5rem", flexDirection: "column" }}>
                                <Typography color="#5B5B5B" fontSize={16}>Phone Number</Typography>
                                <Typography>{profile.phone_number}</Typography>
                            </Box>
                        </Box>
                    </Box>
                </Drawer>
            </Box>
        </Grid>
    );
};


const styles = {
    departmentContainer: {
        display: "flex",
        alignItems: "center",
        gap: "1rem",
        borderBlockEnd: "1px solid #E5E5E5",
        backgroundColor: "#FAFAFA",
        width: "100%",
        justifyContent: "center",
        paddingBlock: 8,
        paddingInline: "1rem",
        cursor: "pointer",
        borderRadius: 8
    },

    textContainer: {
        display: "flex",
        gap: ".5rem",
        flexDirection: "column",
    },
    liveButton: {
        color: "#fff",
        backgroundColor: "#EC1300",
        // backgroundColor: "#C0C0C0",
        borderRadius: "10px",
        fontSize: "8px"
    }
}
